<template>
  <div id="app">
	<router-view></router-view>
    <NavBottom></NavBottom>
  </div>
</template>

<script>
import './assets/fonts/iconfont.js'
import NavBottom from './components/NavBottom.vue'
export default {
  name:'App',
  components:{
    NavBottom
  }
}
</script>


<style>

/* css */
body *{
	user-select: none;	/* iosandroid */
	-webkit-touch-callout: none;	/* iosios&android */
	-webkit-text-size-adjust: 100%;	/*  */

	/*  */
	font-family: PingFangSC-Light,helvetica,Heiti SC;	
}
a,button,input{
	-webkit-tap-highlight-color: rgba(0,0,0,0);	
  /* androida/button/input 
  & iosa */
}
button,input{
	-webkit-appearance: none;	/* iosbuttoninput */
	border-radius: 0;	/* iosinput */
}

/*  */
:root{
	--c1:#dd1a21;
}
body{
	margin: 0;
	font-size: 12px;
	background-color: rgb(245, 245, 245);
	color: #333;
}
a {
	text-decoration: none;
	color: #333;
}
input {
	outline: none;
	vertical-align: middle;
	padding: 0;
}
ul{
	margin: 0;
	padding: 0;
	list-style: none;
}
img{
	vertical-align: middle;
}
h1,h2,h3,h4,p{
	margin: 0;
}
html{
	font-size: 26.666666666vw;
}
.clearfix:after{/* */
        content: "";
        display: block;
        height: 0;
        clear:both;
        visibility: hidden;
    }
.clearfix{
	*zoom: 1;/*ie6 *IE6-IE7*/
}

/*  */
@font-face {
	font-family: 'iconfont';
  src: url('./assets/fonts/iconfont.eot');
	src: url('./assets/fonts/iconfont.eot?#iefix') format('embedded-opentype'),
		url('./assets/fonts/iconfont.woff2') format('woff2'),
		url('./assets/fonts/iconfont.woff') format('woff'),
		url('./assets/fonts/iconfont.ttf') format('truetype'),
		url('./assets/fonts/iconfont.svg#iconfont') format('svg');
}
.iconfont {
	font-family: "iconfont" !important;
	font-size: 16px;
	font-style: normal;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

.icon {
  width: 1em;
  height: 1em;
  vertical-align: -0.15em;
  fill: currentColor;
  overflow: hidden;
}

/* ----------------------------------------------------------------------------------------------- */

</style>



